.prompt {
  position: absolute;
  /*opacity: 0;*/
  visibility: hidden;
  font-size: 14px;
  width: 211px;
  color: #FF3E28;
  font-style: italic;
  text-align: center;
  bottom: 50px;
  left: 0;
  right: 0;
  margin: auto;
}

#preloader {
  /*visibility: hidden;*/
  /*opacity: 0;*/
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  overflow: visible;
  background: #fbfbfb url("../images/common/preloader_1.gif") no-repeat center center;
}

*[language] {
  opacity: 0;
}

.free-lesson-success__elem > span {
  text-decoration: underline;
  cursor: pointer;
  color: #F8D6B0;
}