.prompt {
  position: absolute;
  opacity: 0;
  font-size: 14px;
  width: 211px;
  color: #FF3E28;
  font-style: italic;
  text-align: center;
  bottom: 70px;
}

.wrap {
  display: flex;
  max-width: 420px;
  width: 100%;
  margin: 0 auto;
}

.cancel-btn {
  background-color: #4C9CD8;
  width: 200px;
  height: 45px;
  line-height: 43px;
  margin: 0 auto;
}

#preloader {
  /*visibility: hidden;*/
  /*opacity: 0;*/
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  overflow: visible;
  background: #fbfbfb url("../images/common/preloader_1.gif") no-repeat center center;
}